.advantage__list {
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 480px) {
    flex-direction: row;
    justify-content: baseline;

  }
}
.advantage__list-item {
  display: flex;

  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
  @media screen and (min-width: 480px) {
    width: calc((100% - 152px) / 2);
    padding-bottom: 40px;
    margin-bottom: 0;

  }
  @media screen and (min-width: 768px) {
    width: calc((100% - 32px) / 2);
  }

}
.advantage__item {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 50px;
  height: 50px;
  background-color: #f8d24e;
  border-radius: 25px;
}

.advantage__list-icon {
  width: 36px;
  height: 36px;
  background-color: #f8d24e;
  border-radius: 25px;
  fill: black;
}
.name-container {

  @include font(700, 36px, 42px);

  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #fcfcfc;
  margin-top: 16px;
  margin-bottom: 18px;

  @media screen and (min-width: 480px) {

    text-transform: none;
    font-size: 48px;
    line-height: 59px;
    margin-top: 32px;
    margin-bottom: 16px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 71px;
    margin-top: 40px;
  }
}
.advantage__discribe {
  width: 100%;

  @include font(700, 12px, 20.4px);
  text-align: center;
  color: #fcfcfc;

  @media screen and (min-width: 320px) {
    max-width: 230px;
  }
  @media screen and (min-width: 768px) {

    max-width: 152px;
    @include font(400, 14px, 24.36px);

    letter-spacing: 0.02em;
  }
  @media screen and (min-width: 1200px) {
    padding: 0 20px;
    max-width: 368px;
    @include font(400, 16px, 28.16px);
  }
}
.advantage__list-item:last-child {
  margin-bottom: 0;
  padding-bottom: 60px;
  @media screen and (min-width: 480px) {
    width: calc((100% - 152px) / 2);

    padding-bottom: 40px;

  
    margin-bottom: 200px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 180px;

  }
}
