.contacts {
    width: 100%;
    background-color: #84A178;
}  
.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}
.contacts__item {
    margin-left: auto;
margin-right: auto;
    max-width: 280px;
    background: #FCFCFC;
box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
border-radius: 24px;
padding: 40px 32px;
margin-bottom: 20px;
}
.contacts__subtitle {
width: 80px;
background-color: #F8D24E;
border-radius: 4px;
margin-bottom: 20px;
@include font(500, 12px, 22px);
text-align: center;
text-transform: uppercase;
letter-spacing: 0.04em;
}
.contacts__subtitle_width {
    width: 130px;
}
.contacts__group {
    padding-top: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #E1E1E1;
}
.contacts__group_not-padding-top {
    padding-top: 0;
}
.contacts__group_not-padding-bottom {
    padding-bottom: 0;
    border-bottom: 0;
}
.contacts__text {
@include font(500, 12px, 22px);
letter-spacing: 0.04em;
margin-bottom: 8px;
}
.contacts__address {
@include font(500, 12px, 22px);
letter-spacing: 0.04em;
color: #D6936D;
}
.contacts__weekdays {
@include font(500, 12px, 22px);
}
.contacts__time {
@include font(500, 12px, 22px);
color: #ABABAB;
}
.contacts__weekdays_margin {
margin-top: 8px;
}
.contacts__tel,
.contacts__email {
@include font(500, 12px, 18px);
color:#131313;
}
.contacts__list {
margin-bottom: 32px;
}
.contacts__button_primary {
width: 196px;
margin-bottom: 16px;
margin-left: auto;
margin-right: auto;
}
.contacts__button_secondary {
width: 196px;
margin-left: auto;
margin-right: auto;
}

@media screen and(min-width: 768px) {
.contacts__list {
    display: flex;
    column-gap: 16px;
}
.contacts__item {
    max-width: 224px;
    padding: 40px 24px;
    margin-bottom: 0;
}
.contacts__subtitle {
    width: 73px;
    border-radius: 6px;
    @include font(500, 16px, 30px);
        }
.contacts__subtitle_width {
    width: 138px;
        }
.contacts__group {
    padding-top: 32px;
    padding-bottom: 32px;
}
.contacts__text {
    @include font(500, 14px, 26px);
    margin-bottom: 12px;
}
.contacts__address {
     @include font(500, 14px, 26px);
}
.contacts__weekdays {
     @include font(500, 14px, 26px);
        }
.contacts__time {
    @include font(500, 14px, 26px);
}
.contacts__weekdays_margin {
    margin-top: 12px;
}
.contacts__tel,
.contacts__email {
    @include font(500, 14px, 26px);
}
.contacts__list {
    margin-bottom: 60px;
}
.contacts__button {
    display: flex;
    column-gap: 16px;
    align-items: center;
    justify-content: center; 
}
.contacts__button_primary {
    margin: 0;
}
.contacts__button_secondary {
    margin: 0;
}
}

@media screen and(min-width: 1200px) {
.contacts__list {
    column-gap: 32px;
}
.contacts__item {
    max-width: 368px;
    padding: 40px;
}
.contacts__subtitle {
    width: 75px;
}
.contacts__subtitle_width {
    width: 138px;
}
.contacts__text {
    @include font(500, 16px, 30px);
margin-bottom: 8px;
}
.contacts__address {
    @include font(500, 16px, 29px);
}
.contacts__weekdays {
    @include font(500, 16px, 30px);
}
.contacts__time {
    @include font(500, 16px, 30px);
}
.contacts__tel,
.contacts__email {
    @include font(500, 16px, 29px);
}
.contacts__list {
    margin-bottom: 104px;
}        
}